
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap');
/* like global variables for use over and over */

/************************************/
 /************** Root ************/
/************************************/
:root {
  --primary-white: white;
  --primary-orange: #ff4e00;
  --primary-blue: #748cab;
  --primary-dark: #2a2b2a;
  --primary-about: #d2b48c;
  --primary-projects: #fdf9eb;
}


/************************************/
 /************** Body ************/
/************************************/
html {
  overflow-x: hidden;
}

body {
  font-family: 'Roboto Slab', serif;
  margin: 0 auto;
  overflow-x: hidden;
  width: auto;
}

/************************************/
 /************** NavBar ************/
/************************************/


.navbar-dark {
  background-color: var(--primary-dark) !important;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0,0,0,.75);
  -moz-box-shadow: 0 2px 4px 0 rgba(0,0,0,.75);
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.75);
}
.navbar-toggler {
  border: none !important;
  color: var(--primary-orange) !important;
}

.my-name {
  font-weight: 500;
  color: var(--primary-white);
  font-size: 2rem;
  margin-top: 0.5rem;
  margin-left: 1rem;
  text-transform: uppercase;
  font-family: 'shadows into light';
  letter-spacing: 0.1rem;
  text-decoration: none;
}

.my-name:link { text-decoration: none; }


.my-name:visited { text-decoration: none; }


.my-name:hover { text-decoration: none;
                  color: var(--primary-orange); }


.my-name:active { text-decoration: none; }



nav a.nav-link {
  font-weight: 400;
  letter-spacing: 0.1rem;
  margin-top: 0.5rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
  text-align: center;
  font-family: 'Roboto Slab', serif;
}

.navbar-nav > li > a {
  margin-right: 1rem;
  margin-left: 1rem;
  width: 6.5rem;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid var(--primary-white);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid var(--primary-orange);
}

@media(max-width: 767px) {
  .header-message-container {
    display: block;
    margin: 0 auto;
}

  .my-name {
    font-weight: 400;
  }
  .navbar-nav > li {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
  }

  .navbar-nav > li > a {
    width: 10rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-top: none;
    border-radius: 1rem;
  }

  .navbar-nav > li > a:hover {
    border-top: none;
    color: var(--primary-orange) !important;
  }
  
  .navbar-nav > li > a.active, .navbar-nav > li > a.active:focus {
    border-top: none;
    color: var(--primary-orange) !important;
  }

  #home {
    background-color: #3a3a3a;
  }
  
  #about{
    background-color: #3a3a3a;
  }

  #resume {
    background-color: #3a3a3a;
  }
}

