/************************************/
 /************** Projects ************/
/************************************/

.project-outer-wrapper {
  position: relative;
  z-index: 2;
  /* clip-path: polygon(100% 0, 100% 100% ,0 calc(100% - 10vw), 0 0); */
  /* margin-bottom: -10vw; */
}
/* uncomment these three to get back angled line. */
.project-wrapper {
  position: relative;
  background-color: var(--primary-projects);
  height: auto;
  /* padding-bottom: 20vh; */
  padding-top: 4rem;
}

.projects-title {
  display: flex;
  margin: 0 auto;
  /* margin-bottom: 1rem; */
  width: fit-content;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-family: 'Roboto Slab', serif;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 2.5rem;
  color: var(--primary-dark);
  padding: 0rem 0.5rem 0.5rem 0.5rem;
  border-bottom: 0.0625rem solid var(--primary-dark);
}

.text-highlight {
  font-size: 1.35rem;
  font-weight: bolder;
  color: var(--primary-white);
  text-shadow: 2px 2px #000;
}

.gallery-wrapper {
  display: flex;
  height: auto;
  width: 100%;

  /* padding-left: 22.5vw; */
  /* padding-right: 22.5vw; */
}

.projects {
  height: auto;
  display: flex;
  padding-top: 1.5rem;
  padding-bottom: 2.5rem;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.projects_colum {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* width: 100%; */
  max-width: 50rem;
}

.project_card {
  background-color: var(--primary-white);
  background-size: cover;
  padding: 6rem 0 0;
  margin: 0.75rem;
  /* flex-grow: 1; */
  border-radius: 2rem;
  width: 36ch;
  max-width: 36ch;
  height: 20rem;
  overflow: hidden;
  transition: transform 400ms ease;
  color: var(--White);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.project_card:hover,
.project_card:focus-within {
  transform: scale(1.05) !important;
}

.card_content {
  --padding: 1.75rem;
  padding: var(--padding);
  background: linear-gradient(
      hsl(0 0% 0% / 0),
      hsl(0 0% 0% / 0.25),
      hsl(0 0% 0% / 0.50),
      hsl(0 0% 0% / 0.65) 15%,
      hsl(0 0% 0% / 1)
      );
}


.project_card:hover .card_content > *:not(.card_title),
.project_card:focus-within .card_content > *:not(.card_title) {
  opacity: 1;
  /* For when card content will show up */
  transition-delay: 575ms;
}


.card_title {
  position: relative;
  width: max-content;
  padding-bottom: 0.25rem;
  font-family: 'Roboto Slab', serif;
  font-size: 1.75rem;
  text-transform: uppercase;
  color: var(--primary-white);
}

.card_title::after {
  content: "";
  position: absolute;
  left: calc(var(--padding) * -1 );
  bottom: 0;
  height: 4px;
  width: calc(100% + var(--padding));
  border-top-right-radius: 2rem;
  border-bottom-right-radius: 2rem;
  background: var(--primary-orange);
  /* For orange line on project card hover*/
  transition: transform 400ms ease;
  transform-origin: left;
  
}

.project_card:hover .card_title::after,
.project_card:focus-within .card_title::after {
  transform: scaleX(1);
}

.card_body {
  color: rgb(255 255 255 / .95);
  padding-top: 0.25rem;
  padding-bottom: 0.80rem;
  font-family: 'Roboto Slab', serif;
  font-size: 1.25rem;
  text-shadow: 1px 1px black;
}

/* project button */
.button {

  background-color: transparent;
  border: 2px solid var(--primary-white);
  color: var(--primary-white);
  text-align: center;
  border-radius: 0.5rem;
  font-size: 1.15rem;
  font-weight: bolder;
  text-decoration: none;
  padding: 0.50rem 1rem !important;
  margin-bottom: 1rem !important;
  
}

.button:hover,
.button:focus {
  text-decoration: none;
  background-color: var(--primary-orange);
  border: 2px solid var(--primary-orange);
  color: var(--primary-white);
  transition: .2s ease-in-out !important;
}


