/* Square portion for arrows in about section */
.square {
  position: relative;
  
}

.square::after,
.square::before {
  position: absolute;
  content: '';
  align-items: center;
  justify-content: center;
  width: 15rem;
  height: 8rem;
  background: var(--primary-about);
  bottom: -1em;
  
}

.square::before {
  right: 50%;
  transform: skew(0, 20deg);

}

.square::after {
  left: 50%;
  transform: skew(0,-20deg);

}
