  border-radius: 1rem;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  border: 2px solid var(--primary-white);
  color: var(--primary-white);
  background-color: transparent;
}

.submit-message:hover {
  background-color: var(--primary-orange);
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

.external-links-wrapper {
  display: flex;
  margin: 0 auto;
  /* margin-top: 1rem; */
  align-content: center;
  justify-content: center;
  width: 100%;
}

.backToTopLink {
  margin-left: 80vw;
  margin-top: 1rem;
  margin-bottom: 1rem;
  /* position: absolute; */
  /* z-index: 10; */
  /* bottom: 1; */
  /* right: 1; */
}

#backToTop {
  display: flex;
  align-items: center;
  justify-content: center;
  /* align-self: center; */
  color: var(--primary-white);
  width: 7.5ch;
  height: 7.5ch;
  border-radius: 100%;
  border: 4px solid white;
}

#upArrow {
  font-size: 1.75rem;
}

#backToTop:hover {
  color: var(--primary-orange);
  border: 4px solid var(--primary-orange);
  transition: .2s ease-in-out;
}

.githubIcon {
  font-size: 3.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  color: var(--primary-white);
}

.linkedinIcon {
  font-size: 3.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  color: var(--primary-white);
}

.githubIcon:hover {
  color: var(--primary-orange);
  transition: .2s ease-in-out !important;
}

.linkedinIcon:hover {
  color: var(--primary-orange);
  transition: .2s ease-in-out !important;
}

.footers {
  color: var(--primary-white);
  width: 100%;
  /* padding-top: 1rem; */
  padding-bottom: 2.5rem;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Roboto Slab', serif;
  z-index: 10;
}

#projectFooters {
  padding-top: 2.5rem;
}


#name {
  color: var(--primary-white);
}

#email {
  color: var(--primary-white);
}

#message {
  color: var(--primary-white);
}

@media(max-width: 767px) {

  .contact-info {
    height:auto;
    grid-template-columns: repeat(1, 1fr);
  }
  
  .contact-me-title {
    padding: 0 0 0 0;
  }

  .contact-query {
    padding-bottom: 2rem;
  }

  .input-titles {
    width: 90%;
  }

  .name {
    width: 90%;
  }

  .email {
    width: 90%;
  }

  .message {
    width: 90%;
    padding-bottom: 8rem;
  }

  .button-container {
    display: flex;
    position: relative;
    width: 100%;
    margin-top: 0rem;
  }

  .submit-message {
    width: 10rem;
    margin: 0 auto;
    font-size: 1.15rem;
    padding: 1rem 1rem 1rem 1rem;
    margin-bottom: 1rem;
  }

  .githubIcon {
    font-size: 3.8rem;
    margin: 0.5rem 0rem 0.5rem 0rem;
  }
  
  .linkedinIcon {
    font-size: 3.8rem;
    margin: 0.5rem 0rem 0.5rem 0rem;
  }

}


/* For projects on tablet size */
@media(max-width: 1120px) {
  .projects {
    display: block;
    margin: 0 auto;
  }
  .projects_colum {
    max-width: none;
  }
}


/* MOBILE PHONE USERS */
@media(max-width: 428px) {
  .my-name {
    font-size: 1.25rem;
  }

  .backToTopLink {
    display: none;
  }
  .external-links-wrapper{
    padding-top: 2rem;
  }
  .header-wrapper {
    height: 600px;
  }
  .project_card {
    max-width: 24ch;
    padding-top: 5rem;
  }
  .card_content {
    padding: 0.5rem;
    padding-bottom: 5rem !important;
  }
  .card_content:hover{
    transform: translateY(92%);
  }
  .card_title{
    font-size: 1.35rem
  }
  


}

